import React from "react";
import { Text } from "react-native";
import { render } from "@testing-library/react-native";

describe("Module de Planification de Projets Personnels", () => {
  // Fonctions simulées
  const creerProjet = (nom, budgetTotal) => ({
    nom,
    budgetTotal,
    depenses: 0,
  });

  const allouerDepense = (projet, montant) => {
    projet.depenses += montant;
    return projet;
  };

  const calculerProgression = (projet) => {
    return Math.round((projet.depenses / projet.budgetTotal) * 100);
  };

  it("crée correctement un projet personnel", () => {
    const projet = creerProjet("Déménagement", 150000);
    expect(projet.nom).toBe("Déménagement");
    expect(projet.budgetTotal).toBe(150000);
    expect(projet.depenses).toBe(0);
    console.log("✅ Projet personnel créé correctement");
  });

  it("alloue correctement un montant aux dépenses du projet", () => {
    let projet = creerProjet("Voyage", 200000);
    projet = allouerDepense(projet, 50000);
    expect(projet.depenses).toBe(50000);
    console.log("✅ Dépense allouée correctement au projet");
  });

  it("calcule correctement la progression du projet", () => {
    let projet = creerProjet("Formation", 100000);
    projet = allouerDepense(projet, 25000);
    const progression = calculerProgression(projet);
    expect(progression).toBe(25);
    console.log("✅ Progression du projet calculée correctement");
  });

  it("simule l'affichage des projets et de leur progression", () => {
    const DummyComponent = () => (
      <>
        <Text>Projet: Déménagement - Progression: 0%</Text>
        <Text>Projet: Voyage - Progression: 25%</Text>
      </>
    );

    const { getByText } = render(<DummyComponent />);
    expect(getByText("Projet: Déménagement - Progression: 0%")).toBeTruthy();
    expect(getByText("Projet: Voyage - Progression: 25%")).toBeTruthy();
    console.log("✅ Composants des projets et de leur progression affichés correctement");
  });
});
